import { Toast } from 'mint-ui';
const actions = {
    navTitle: ({ commit, state }, opt) => {
        state.navTitle = opt;
        commit("navTitle");
    },
    addCart: ({ commit, state }, opt) => {
        if (state.carts.length == 0) {
            state.carts.push(opt);
            window.sessionStorage.setItem('carts', JSON.stringify(state.carts));
            Toast({
                message: '成功添加至购物车！',
                iconClass: 'icon icon-success'
            });
        } else {
            var eleName = [];
            state.carts.forEach(ele => {
                eleName.push(ele.name);
            });
            if (eleName.indexOf(opt.name) != -1) {
                Toast('物品已存在，请勿重复添加！');
            } else {
                state.carts.push(opt);
                window.sessionStorage.setItem('carts', JSON.stringify(state.carts));
                Toast({
                    message: '成功添加至购物车！',
                    iconClass: 'icon icon-success'
                });
            }
        }
        //commit("addCart");
    },
    // total: ({ commit, state }) => {
    //     state.carts.forEach(ele => {
    //         state.total += ele.value * Number(ele.price)
    //     });
    //     commit("total");
    // }
};

export default actions;


