<template>
   <div class="my">
       <Vheader></Vheader>
       <div class="content">
            <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
            <mt-field label="邮箱" placeholder="请输入邮箱" type="email" v-model="email"></mt-field>
            <mt-field label="QQ" placeholder="QQ" type="text" v-model="password"></mt-field>
            <mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
            <mt-field label="网站" placeholder="请输入网址" type="url" v-model="website"></mt-field>
            <mt-field label="数字" placeholder="请输入数字" type="number" v-model="number"></mt-field>
            <mt-field label="生日" placeholder="请输入生日" type="date" v-model="birthday"></mt-field>
            <mt-field label="自我介绍" type="textarea" rows="4" v-model="introduction"></mt-field>
       </div>
       <Vfooter :isFixed="isFixed"></Vfooter>
   </div>
</template>

<script>
import Vheader from './Vheader';
import Vfooter from './Vfooter';
export default {
    name:"Vmy",
    components:{
      Vheader,
      Vfooter
    },
    data(){
        return {
           username:'bin',
           email:'zlgbmail@163.com',
           password:813254038,
           phone:'110',
           website:'https://gitee.com/Dream_bin/projects',
           number:888,
           birthday:'1993-03-05',
           introduction:'好好学习，天天向上！',
           isFixed:true
        }
    },
    mounted(){
        var inputs=document.querySelectorAll('input');
        let i=0;
        const len=inputs.length;
        for(i;i<len;i++){
            inputs[i].onfocus=()=>{
                this.isFixed=false;
                document.querySelector('.mint-tabbar').style.position='static';
            };
            inputs[i].onblur=()=>{
                this.isFixed=true;
                document.querySelector('.mint-tabbar').style.position='fixed';
            };
        }
        document.querySelector('textarea').onfocus=()=>{
            this.isFixed=false;
            document.querySelector('.mint-tabbar').style.position='static';
        };
        document.querySelector('textarea').onblur=()=>{
            this.isFixed=true;
            document.querySelector('.mint-tabbar').style.position='fixed';
        }
        
    }
}
</script>

<style scoped>

</style>



