<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import {mapState ,mapGetters, mapActions,mapMutations } from 'vuex';
import './components/common/rem';
export default {
  name: 'App',
  data(){
    return {
      isShow:this.$store.state.isShow
    }
  },
}
</script>

<style>
@import url('./components/common/resetCSS');
/* 引入阿里字体 */
@import url('./components/font/iconfont.css');
/* 引入项目通用样式 */
@import url('./components/common/common');
html,body,#app{
  width: 100%;
  height: 100%;
}
</style>



