import { Toast } from 'mint-ui';
import { MessageBox } from 'mint-ui';
const mutations = {
    navTitle(state) {
        state.navTitle = state.navTitle;
    },
    //添加购物车
    addCart(state) {
        state.carts = state.carts;
    },
    //增加
    add(state, opt) {
        state.carts.forEach(ele => {
            if (ele.name == opt) {
                ele.value++;
            }
        });
        window.sessionStorage.setItem('carts', JSON.stringify(state.carts));
    },
    //减少
    reduce(state, opt) {
        state.carts.forEach(ele => {
            //console.log(ele);
            if (ele.name == opt) {
                ele.value = ele.value-- <= 1 ? 1 : ele.value--;
                if (ele.value <= 1) Toast('再减少就没啦！');
            }

        });
        window.sessionStorage.setItem('carts', JSON.stringify(state.carts));
    },
    //删除
    del(state, opt) {
        //console.log(state.carts);
        state.carts.forEach(ele => {
            if (ele.name == opt) {
                state.carts.splice(state.carts.indexOf(ele), 1);
            }
        });
        window.sessionStorage.setItem('carts', JSON.stringify(state.carts));

    },
    //删除所有
    delAll(state) {
        state.carts = [];
        window.sessionStorage.setItem('carts', JSON.stringify(state.carts));
    },
    //总额
    // total(state) {
    //     state.total = state.total;
    //     console.log('mutatins3', state.total);
    // }
};


export default mutations;


