<template>
    <div class="cart">
        <div class="goods_header">
            <i class="mobile-icon-fanhui back" @click="back()" v-show="carts.length!=0"></i>
            <!-- <mt-button icon="back" class="back" @click="back()"></mt-button> -->
            <h2>{{navTitle}}</h2> 
        </div> 
        <div class="content">
            <div v-if="carts.length==0" class="empty">
                <p><img src="../../static/imgs/gouwuche.png"></p>
                <p class="remind">购物车是空的哦，快去购物吧</p>
                <mt-button type="danger" @click="$router.push('/')">逛一逛</mt-button>
            </div>
            <div v-if="carts.length!=0" class="content_list">
                <ul>
                    <li v-for="(v,i) in carts" :key="i" class="produce cl">
                        <img v-lazy="v.imgOne" class="fl left">
                        <div class="fl mid">
                            <p>{{v.name}}</p>
                            <p class="price">{{v.price*v.value | money('￥')}}</p>
                            <a class="reduce btn" @click="reduce(v.name)">-</a>
                            <input type="text" v-model="v.value" class="num">
                            <a class="add btn" @click="add(v.name)">+</a>
                        </div>
                        <div class="del fr right">
                            <mt-button size="large" type="danger" @click="del(v.name)">删除</mt-button>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <div class="goods_footer cl"  v-show="carts.length!=0">
            <div class="fl total">合计：<span>{{total | money('￥')}}</span> </div>
            <button class="fr clear" @click="delAll">清空购物车</button>
            <button class="fr continue" @click="$router.push('/')">继续购物</button>
        </div>
        <Vfooter v-show="carts.length==0" :isFixed="isFixed"></Vfooter>
    </div>
</template>


<script>
import { mapState, mapActions, mapMutations, mapGetters } from "vuex";
import Vfooter from "./Vfooter";

export default {
  name: "Vcart",
  computed: {
    ...mapGetters(["navTitle"]),
    carts() {
      return this.$store.state.carts;
    },
    total(){
        let money=0;
        this.carts.forEach(ele => {
          money += ele.value * Number(ele.price)
        });
        return money;  
    }
  },
  components: {
    Vfooter
  },
  data() {
    return {
      num: "",
      isFixed:true
    };
  },
  methods: {
    back() {
      this.$router.push("/");
      window.sessionStorage.setItem("navTitle", "首页");
    },
    ...mapMutations(["add", "reduce", "del","delAll"])
  },
  created(){
    
  },
  mounted(){
    //console.log(this.total)
  }
};
</script>

<style scoped>
.goods_header{
  background-color: #26a2ff;
  color: #fff
}
.empty {
  text-align: center;
  padding-top: 1rem;
}
.empty p {
  margin: 0.5rem 0;
}
.empty .remind {
  font-weight: bold;
  font-size: 24px;
  color: #ccc;
}
.empty button {
  width: 30%;
}
.produce {
  padding: 0.2rem;
}
.produce p {
  margin-top: 0.3rem;
  font-size: 14px;
}
.produce p.price {
  color: red;
}
.content_list li img.left {
  width: 30%;
}
.mid {
  width: 60%;
  font-size: 0;
  text-align: left;
}
.mid .btn {
  display: inline-block;
  width: 0.8rem;
  height: 0.8rem;
  line-height: 0.8rem;
  border: 1px solid #ccc;
  margin-top: 0.2rem;
  font-size: 14px;
  vertical-align: middle;
  text-align: center;
}
.mid .num {
  text-align: center;
  vertical-align: bottom;
  width: 0.8rem;
  height: 0.8rem;
  line-height: 0.8rem;
  border: none;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
.right {
  width: 10%;
  margin-top: 0.2rem;
}
.right button {
  font-size: 10px;
}
.goods_footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  line-height: 40px;
  height: 40px;
  font-size: 14px;
}
.goods_footer div {
  width: 30%;
  line-height: 40px;
  height: 40px;
}

.goods_footer button {
  width: 30%;
  line-height: 40px;
  height: 40px;
  margin-left: 2px;
  border: none;
  color: #fff;
  font-size: 16px;
}
.clear {
  background-color: orange;
}
.continue {
  background-color: red;
}
.total span{
    color: red;
    font-size: 18px;
    font-weight: bold;
}
</style>



