<template>
  <div class="info">
       <Vheader></Vheader>
       <div class="content">
           <div v-for="(v,i) in informations" :key="i" :class="'cl info info'+v.id">
              <h2>{{v.title}}</h2>
              <p class="time">{{v.time}}</p>
              <img v-lazy="v.img" alt="图片" @click="open(v.id)">
              <span class="fr more" @click="open(v.id)">阅读全文</span>  
           </div>
       </div>
       <Vfooter :isFixed="isFixed"></Vfooter> 
  </div>
</template>

<script>
import Vheader from "./Vheader";
import Vfooter from "./Vfooter";
export default {
  name: "Info",
  data() {
    return {
      informations: [],
      isFixed: true
    };
  },
  components: {
    Vheader,
    Vfooter
  },
  created() {
    this.$http
      .get("../../static/information.json")
      .then(res => {
        this.informations = res.data.informations;
      })
      .catch(() => {
        var obj = {
          informations: [
            {
              id: 1,
              title: "vivo杀出重围成为黑马销量迅速上升",
              time: "2017-11-15 11:00:10",
              img: "http://img.cnmo-img.com.cn/1498_160x120/1497187.png"
            },
            {
              id: 2,
              title: "vivo将扩张六大新市场 包括香港和台湾",
              time: "2017-11-15 11:00:10",
              img: "http://img.cnmo-img.com.cn/1494_160x120/1493799.jpg"
            },
            {
              id: 3,
              title: "战斗民族将用上vivo手机！",
              time: "2017-11-15 11:00:10",
              img: "http://img.cnmo-img.com.cn/1495_160x120/1494158.jpg"
            },
            {
              id: 4,
              title: "何为好的全面屏手机?首先得是好用手机!",
              time: "2017-11-15 11:00:10",
              img: "http://img.cnmo-img.com.cn/1490_160x120/1489925.jpg"
            }
          ],
          list: [
            {
              id: 1,
              title: " vivo杀出重围成为黑马销量迅速上升",
              time: "2017-11-15 11:00:10",
              boxs: [
                {
                  p:
                    "　　【中国 新闻】11月4日，京东实时竞速榜第四天，各类数码商品销售格局发生细微变化，在品牌、产品、店铺、单品的的排名中皆出现黑马，品牌争霸团战激烈可见一斑。"
                },
                {
                  img: "http://img.cnmo-img.com.cn/1498_600x1000/1497189.png"
                },
                {
                  p:
                    "　　手机产品榜单由于iPhone X的上市京东作为iPhone X最大的线上销售渠道，苹果在榜单内排名一直居高不下，而令我们以外的是，榜单上一改前两日荣耀与小米的拉锯战，另一国产手机品牌vivo惊艳逆袭，强势突围杀入榜单第二名，vivo X20在单品排名榜单上也短时间内登顶成功，成为手机榜单第三日最大黑马，截止至当日晚些时候vivo官方旗舰店的销售总额成为当日最大赢家。除此之外诺基亚手机在当天也再次入榜，这位久违的老朋友入榜让我们看到额此次京东双11活动对手机市场所带来的巨大影响。另外值得我们注意的是三星galaxy也出现在我们的榜单中，对这款手机未来的表现值得我们继续期待。"
                },
                {
                  img: "http://img.cnmo-img.com.cn/1498_600x1000/1497188.png"
                },
                {
                  p:
                    "　　PC方面一反前几日联想一家独大形势，戴尔厚积薄发顺利折桂，联想屈居第二。华硕也因戴尔的异军突起屈居第三，但是依旧超越了ThinkPad和苹果。小米超越宏碁、惠普屈居第六，短短几天局势便如此扑朔迷离，不知在接下来的几天里PC榜单还会为我们带来多少惊喜，让我们更加期待最后的赢家是谁"
                },
                {
                  p:
                    "　　根据小编了解的消息，手机榜单上的黑马vivo在同一天因为未知的原因在苏宁促销活动中下架，反观京东方面vivo却一跃成为最强黑马，这其间原有我们并不知晓，但那时可以看出不同电商品牌在销售预期上还是有很大差别的，所以最终的结果，不管是数码产品品牌之间，还是在电商之间，都还有着巨大的不可确定因素，我们也将继续关注双11各大电商品牌以及数码产品的销售情况，希望大家继续关注。"
                }
              ]
            },
            {
              id: 2,
              title: " vivo将扩张六大新市场 包括香港和台湾",
              time: "2017-11-15 11:00:10",
              boxs: [
                {
                  p:
                    "　　【手机中国 新闻】借助线下销售渠道和产品的精准定位，最近两年来vivo发展越发的好了，它不仅是中国前三大手机厂商之一，也挺进了世界前五大厂商之列，同时它也是下两届FIFA世界杯锦标赛的官方赞助商。为了进一步扩大战果，vivo昨日还宣布，未来几个月将把覆盖的版图扩展到六大新市场。"
                },
                {
                  img: "http://img.cnmo-img.com.cn/1494_600x1000/1493800.jpg"
                },
                {
                  p:
                    "　　据称，vivo将很快扩展到台湾、香港和新加坡三地。同时，其产品还会在俄罗斯销售，以便打开欧洲市场。另外，vivo产品也会在扩展到非洲市场，首先会在摩洛哥和肯尼亚销售。"
                },
                {
                  p:
                    "　　目前，vivo的海外市场已包括印度、泰国，菲律宾、缅甸、马来西亚、印度尼西亚、巴基斯坦、柬埔寨和孟加拉国。"
                },
                {
                  p:
                    "　　vivo表示，将旨在提供能够“激发更多年轻人的信心和自我表达”的设备，这意味着它会继续专注于相机和音乐体验。vivo最新推出的vivo V7 +，就拥有2400万像素自拍镜头，配有自拍闪光灯，支持Face Beauty 7.0和人像模式。预计不仅的将来，世界各地越来越多的人会被vivo手机照亮，并感受vivo手机带来的生活美好。"
                }
              ]
            },
            {
              id: 3,
              title: "我骄傲！联想服务器保持88项世界纪录！",
              time: "2017-11-15 11:00:10",
              boxs: [
                {
                  p:
                    "　　【手机中国 新闻】在经济全球化与互联网蓬勃发展的今天，任何厂家的任何产品都有机会销售到全球，手机当然也不例外。日前，据俄罗斯媒体称，中国手机品牌vivo表示，得益于vivo V7+在印度、泰国、菲律宾、缅甸、马来西亚、印度尼西亚、巴基斯坦、柬埔寨以及孟加拉国取得的成功，vivo计划全面扩展销售市场及销售渠道。vivo产品将在俄罗斯展开销售，俄罗斯将成为vivo进入欧洲市场的第一个国家，另外新加坡以及非洲市场也在计划之内。"
                },
                {
                  img: "http://img.cnmo-img.com.cn/1495_600x1000/1494168.jpg"
                },
                {
                  p:
                    "　　从2014年开始，我们就不难发现，vivo已经开始走国际路线，vivo在全球建立了七大研发中心，主攻5G、人工智能、拍照。另外2015年，vivo总冠名赞助印度板球超级联赛（IPL）、2016年vivo与NBA达成战略合作，成为NBA中国官方手机市场合作伙伴，今年5月，vivo宣布与国际足球联合会（FIFA）达成为期6年的FIFA世界杯全球赞助合作，贯穿两届比赛。这意味着vivo将连续成为2018年及2022年两届世界杯全球官方赞助商，而其中2018年的世界杯就是在俄罗斯举办。"
                },
                {
                  img: "http://img.cnmo-img.com.cn/1495_600x1000/1494169.jpg"
                },
                {
                  img:
                    "　　vivo的全球化布局是非常具有战略性目光的，近年来国外品牌在我国市场份额不断下降，说明我国本土品牌实力的日渐提升，除了vivo外，"
                }
              ]
            },
            {
              id: 4,
              title: "何为好的全面屏手机?首先得是好用手机!",
              time: "2017-11-15 11:00:10",
              boxs: [
                {
                  p:
                    "　　每年，智能手机的创新点都是由苹果引领的，而今年不同，早在苹果iPhone X发布之前，众多国产智能手机早已定下的“全面屏”的发展方向，并通过自主研发的技术与设计，最大限度把“全面屏”手机的完成度提升到极致。对于消费者而言，一时间，数款全面屏手机摆在面前，如何挑选，成为了值得思考的一个新问题。站在厂商的角度而言，如何吸引消费者，让消费者选择自家产品，也是一个对应需要考虑的问题。"
                },
                {
                  p:
                    "　　对此，笔者看来，对于全面屏手机而言，本质上它还是一部手机，全面屏不过是一个提升用户体验的创新功能点而已，故而，想被用户评为好的全面屏手机，首先，必须是一部好用的手机"
                },
                {
                  p:
                    "　　智能手机发展至今，已经成为了人类生活的必需品，随着功能的不断增加，智能手机所覆盖的用户应用场景也在不断增多。根据DataStory公司发布的《2017手机行业白皮书》显示，手机行业目前热度依旧不减，“全面屏”、“人脸识别”等功能点将成为下一个行业热点。"
                },
                {
                  img: "http://img.cnmo-img.com.cn/1490_600x1000/1489930.jpg"
                },
                {
                  img:
                    "　　综上所述，用户在评价一部手机时并不像媒体一样，这个功能如何如何，那个功能如何如何。反而更加简单、直接，“好用”就是一个简单的感觉，拿到一部手机，2个小时后，用户即可说出“好用”还是“不好用”。但对于厂商而言，想要让用户认可你的手机好用，可不是2个小时就能搞定的了，需要真正的从用户实际场景出发，深挖用户在使用手机时的痛点，通过创新功能点来改善原有的痛点，才是一条做好手机的光明大道！“全面屏”时代，如何将全面屏体验做得完整、做得与手机融为一体，带来整体用户体验的提升，这才是一部好的全面屏手机生产商需要深思的问题。"
                }
              ]
            }
          ]
        };
        this.informations = obj.informations;
      });
  },
  mounted: function() {},
  methods: {
    open(id) {
      this.$router.push({ path: "/infos", query: { id: id } });
    }
  }
};
</script>


<style scoped>
.info {
  text-align: center;
  border-bottom: 1px solid #ccc;
  padding: 0.15rem 0;
}
.info:last-child {
  border-bottom: none;
}
.info h2 {
  font-weight: bold;
  font-size: 16px;
}
.info .time {
  margin: 0.1rem 0;
}
.info img {
  height: 176.25px;
  width: 100%;
}
.info .more {
  margin: 0.2rem 0.2rem 0;
}
</style>



