const state = {
    navTitle: window.sessionStorage.getItem('navTitle') ? window.sessionStorage.getItem('navTitle') : 'VIVO',
    carts: window.sessionStorage.getItem('carts') ? JSON.parse(window.sessionStorage.getItem('carts')) : [],
    //total: 0
};

export default state;


