<template>
  <div id="home">
      <vheader></vheader>
      <div class="content">
        <img src="../../static/imgs/official.png" alt="">
        <mt-swipe :auto="4000" :show-indicators="true" class="swipe">
            <mt-swipe-item @click.native="$router.push({path:'/goods',query:{id:4}})">
              <img src="https://shopstatic.vivo.com.cn/vivoshop/commodity/20171113/20171113232814110405_original.jpg">
            </mt-swipe-item>
            <mt-swipe-item @click.native="routerClick('/choose')">
              <img src="https://shopstatic.vivo.com.cn/vivoshop/commodity/20171122/20171122174336166279_original.jpg">
            </mt-swipe-item>
        </mt-swipe>
        <ul class="list cl">
            <li @click="routerClick('/choose')"><img src="../../static/imgs/xuangou.jpg"></li>
            <li @click="routerClick('/part')"><img src="../../static/imgs/peijian.jpg"></li>
            <li @click="linkOne"><img src="../../static/imgs/pingbao.jpg"></li>
            <li @click="linkTwo"><img src="../../static/imgs/dingzhi.jpg"></li>
        </ul>
        <div class="product">
            <h3>爆款产品</h3>
            <div class="product_list">
                <ul class="cl">
                  <li v-for="(v,i) in product_list" :key="i" @click="buy(v.id)">
                      <img v-lazy="v.img">
                      <p><b>{{v.name}}</b></p>
                      <p>{{v.nametwo}}</p>
                      <p class="price">{{v.Price | money('￥')}}</p>
                  </li>
                </ul>
            </div>
        </div>
      </div>
      <vfooter :isFixed="isFixed"></vfooter> 
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import { MessageBox } from "mint-ui";
import Vheader from "./Vheader";
import Vfooter from "./Vfooter";
export default {
  name: "Home",
  data() {
    return {
      product_list: [],
      isFixed: true
    };
  },
  components: {
    Vheader,
    Vfooter
  },
  created() {
    this.$http
      .get("../../static/Home.json")
      .then(res => {
        //console.log(res.data);
        this.product_list = res.data.todos;
      })
      .catch(() => {
        var obj = {
          todos: [
            {
              id: "1",
              img:
                "https://shopstatic.vivo.com.cn/vivoshop/commodity/71/4171_1496689409434hd_530x530.png",
              name: "X9Plus 全网通",
              nametwo: "优惠200元|限量促销",
              Price: "2798"
            },
            {
              id: "2",
              img:
                "https://shopstatic.vivo.com.cn/vivoshop/commodity/40/4440_1508723830538hd_530x530.png",
              nametwo: "6.43英寸大屏|0.1s快速解锁",
              name: "X20Plus 全面屏",
              Price: "3498"
            },
            {
              id: "3",
              img:
                "https://shopstatic.vivo.com.cn/vivoshop/commodity/95/4495_1509454710945hd_530x530.png",
              name: "Y79 全面屏手机",
              nametwo: "前置2400万像素",
              Price: "2498"
            },
            {
              id: "4",
              img:
                "https://shopstatic.vivo.com.cn/vivoshop/commodity/66/4266_1496689781362hd_530x530.png",
              name: "Xplay6 128G版",
              nametwo: "后置双摄|免费镭射镌刻",
              Price: "3998"
            },
            {
              id: "5",
              img:
                "https://shopstatic.vivo.com.cn/vivoshop/commodity/11/4111_1492998667334hd_530x530.png",
              name: "Y55 全网通",
              nametwo: "大屏美颜自拍",
              Price: "1098"
            },
            {
              id: "6",
              img:
                "https://shopstatic.vivo.com.cn/vivoshop/commodity/80/4180_1496689544465hd_530x530.png",
              name: "Y66 全网通",
              nametwo: "5.5英寸大屏|支持花呗分期",
              Price: "1298"
            }
          ]
        };
        this.product_list=obj.todos;
      });
  },
  methods: {
    routerClick(url) {
      if (url == "/choose") {
        this.$store.state.isShow = false;
      }
      this.$router.push(url);
    },
    buy(id) {
      this.$router.push({ path: "/goods", query: { id: id } });
    },
    linkOne() {
      Toast("开发中。。。敬请期待！");
    },
    linkTwo() {
      // Toast('想了解更多，请联系作者：q812354038。。备注：Vue学习');
      Toast({
        message: "想了解更多，请联系作者：q812354038。。备注：Vue学习",
        position: "middle",
        duration: 6000
      });
    }
  },
  mounted() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#home {
  width: 100%;
  height: 100%;
}
.swipe {
  height: 6.5rem;
}
.swipe img {
  height: 6.5rem;
  width: 100%;
}
.list {
  width: 100%;
  margin-top: 0.2rem;
}
.list > li {
  width: 25%;
  float: left;
  text-align: center;
}
.list > li > img {
  width: 80%;
}
.product h3 {
  height: 0.8rem;
  line-height: 0.8rem;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  font-size: 14px;
}
.product_list ul {
  /* padding-bottom: 1rem; */
}
.product_list ul li {
  width: 50%;
  text-align: center;
  float: left;
  padding: 0.3rem 0;
  border-bottom: 1px solid #ccc;
}
.product_list ul li > img {
  width: 80%;
}
.product_list ul li p {
  margin-top: 0.2rem;
}
.product_list ul li p.price {
  margin-top: 0.3rem;
  color: red;
}
</style>



