<template>
    <div class="footer">
        <mt-tabbar v-model="selected" :fixed="isFixed">
            <mt-tab-item id="首页" @click.native="routerClick('/')">
                <p class="mobile-icon-shouye shouye"></p>
                <p class="word">首页</p> 
            </mt-tab-item>
            <mt-tab-item id="资讯" @click.native="routerClick('/info')">
                <p class="mobile-icon-zixun zixun"></p>
                <p class="word">资讯</p> 
            </mt-tab-item>
            <mt-tab-item id="购物车" @click.native="routerClick('/cart')">
                <p class="mobile-icon-gouwuche gouwuche"></p>
                <p class="word">购物车</p> 
            </mt-tab-item>
            <mt-tab-item id="我的" @click.native="routerClick('/my')">
                <p class="mobile-icon-wodedangxuan-copy wode"></p>
                <p class="word">我的</p>  
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>

<script>
import {mapState,mapActions,mapMutations,mapGetters} from 'vuex';
export default {
    name: 'Vfooter',
    data(){
        return {
            selected:window.sessionStorage.getItem('navTitle')
        }
    },
    props:['isFixed'],
    methods:{
        routerClick(url){
            this.$router.push(url);
            window.sessionStorage.setItem('navTitle',this.selected);
            this.$store.dispatch('navTitle',this.selected);
        }
    }
};
</script>

<style scoped>
.footer {
  text-align: center;
}
[class^="mobile-icon"]{
    font-size: 24px;
    color: #26a2ff;
}
p.word{
    margin-top: 0.15rem;
}
</style>



