        </div> 
        <div class="content">
            <mt-swipe :auto="4000" :show-indicators="true" class="swipe">
                <mt-swipe-item>
                    <img  v-lazy="goods.imgOne" :src="goods.imgOne">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img  v-lazy="goods.imgTwo">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img  v-lazy="goods.imgThree">
                </mt-swipe-item>
            </mt-swipe>
            <div class="content_name">
                <h2>{{goods.name}}</h2>
                <p>{{goods.price | money('￥')}}</p>
            </div>
            <div class="content_buy">
                <span class="buy_num">购买数量：</span>
                <a class="reduce btn" @click="reduce(goods)">-</a>
                <input type="text" v-model="num" class="num">
                <a class="add btn" @click="add(goods)">+</a>
            </div>
            <div class="content_bar">
                <mt-navbar v-model="selected" class="tabTitle">
                    <mt-tab-item id="1">图文信息</mt-tab-item>
                    <mt-tab-item id="2">商品配置</mt-tab-item>
                </mt-navbar>

                <!-- tab-container -->
                <mt-tab-container v-model="selected">
                    <mt-tab-container-item id="1">
                        <p v-for="(v,i) in goods.Images" :key="i">
                            <img v-lazy="v.one">
                        </p>
                    </mt-tab-container-item>
                    <mt-tab-container-item id="2">
                       <table>
                           <tr v-for="(v,i) in goods.Peizhis" :key="i">
                               <td class="PeizhiL">{{v.left}}</td>
                               <td class="PeizhiR">{{v.right}}</td>
                            </tr>
                       </table>
                    </mt-tab-container-item>
                </mt-tab-container>
            </div>
        </div>
        <div class="goods_footer">
