}
.content_buy {
  border-bottom: 1px solid #ccc;
  padding: 0.5rem 0;
  font-size: 0;
}
.buy_num {
  font-size: 14px;
  vertical-align: middle;
}
.content_buy .num {
  width: 0.8rem;
  height: 0.8rem;
  text-align: center;
  vertical-align: middle;
  border: none;
  border: 1px solid #ccc;
}
.content_buy .btn {
  border: 1px solid #ccc;
  display: inline-block;
  width: 0.8rem;
  height: 0.8rem;
  line-height: 0.8rem;
  vertical-align: middle;
  font-size: 14px;
}
.content_buy .reduce {
  border-right: none;
}
.content_buy .add {
  border-left: none;
}

.goods_footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  line-height: 56px;
  color: #fff;
  font-size: 16px;
}
.addCart,
.buyNow {
  position: absolute;
  bottom: 0;
  width: 50%;
}
.addCart {
  left: 0;
  background-color: orange;
}
.buyNow {
  right: 0;
  background-color: red;
}
.tabTitle {
  margin-bottom: 0.2rem;
}
table {
  width: 100%;
}
table td {
  padding: 0.3rem 0;
}
table .PeizhiL {
  width: 30%;
}
table .PeizhiR {
  width: 70%;
}
</style>



