import Vue from 'vue';
import Router from 'vue-router';
import Vhome from '@/components/Vhome';
import Vinfo from '@/components/Vinfo';
import Vinfo_list from '@/components/Vinfo_list';
import Vgoods_list from '@/components/Vgoods_list';
import Vchoose from '@/components/Vchoose';
import Vcart from '@/components/Vcart';
import Vmy from '@/components/Vmy';
import Vparts from '@/components/Vparts';


Vue.use(Router);

export default new Router({
    routes: [
        { path: '/', name: 'Vhome', component: Vhome },
        { path: '/goods', name: 'Vgoods_list', component: Vgoods_list },
        { path: '/info', name: 'Vinfo', component: Vinfo },
        { path: '/infos', name: 'Vinfo_list', component: Vinfo_list },
        { path: '/choose', name: 'Vchoose', component: Vchoose },
        { path: '/cart', name: 'Vcart', component: Vcart },
        { path: '/my', name: 'Vmy', component: Vmy },
        { path: '/part', name: 'Vparts', component: Vparts },
        { path: '*', redirect: '/' }
    ]
})


